import React from "react";
import BlinkingText from "../BlinkingText";
import {Theme} from "../../../typings";

const TerminalWindow: React.FC<TerminalWindowProps> = ({selectedTheme, firstLineDone}) => {
    const [secondLineDone, setSecondLineDone] = React.useState(false);
    const [thirdLineDone, setThirdLineDone] = React.useState(false);
    const [fourthLineDone, setFourthLineDone] = React.useState(false);
    const [fifthLineDone, setFifthLineDone] = React.useState(false);
    const [sixthLineDone, setSixthLineDone] = React.useState(false);

    const terminalTextColor = selectedTheme.mainColor;

    React.useEffect(() => {
        if (secondLineDone) {
            setTimeout(() => setThirdLineDone(secondLineDone),800);
        }
    }, [secondLineDone])

    React.useEffect(() => {
        if (fourthLineDone) {
            setTimeout(() => setFifthLineDone(fourthLineDone), 800);
        }
    }, [fourthLineDone])


    const createLinuxPath = () => {
        return <span style={{
            paddingLeft:'0.5%',
            fontWeight:'bolder',
            backgroundColor: selectedTheme.secondaryColor,
            marginRight: '1%',
            color: selectedTheme.tertiaryColor,
            borderBottomRightRadius:'100px',
            borderTopRightRadius:'100px',
        }}> mads@norbypc~/ ={'> '}</span>
    };

    const terminalWindow = () => (<div id='terminal-window' style={{
        margin:'2%',
        fontFamily: 'Roboto Mono, monospace',
        fontSize:'120%',
        backgroundColor:'#15232e',
        color: terminalTextColor,
        marginBottom: '1%',
        height: '80%',
        boxShadow: '0 3px 10px rgb(0 0 0 / 0.2)',
    }}>
        <div style={{
            backgroundColor:'#232b2b',
            border:'1px solid black',
            height: '10%',
            textAlign:'center',
            color:'white',
            boxShadow: '0 3px 10px rgb(0 0 0 / 0.2)',
        }}>{'mads@norbypc~/'}</div>
        <div style={{padding:'1%'}}>
            {
                firstLineDone ? <div style={{fontSize: '100%'}}>{createLinuxPath()}
                    <BlinkingText selectedTheme={selectedTheme} speed={2} setPrevLineDone={setSecondLineDone}
                                  words={['ecf', 'echo $EMAIL_ADDRESS']}/>
                </div> : <></>
            }
            {
                secondLineDone ? <div style={{fontSize: '100%', color: terminalTextColor}}>{' > '}
                    <span>
                        <a href='mailto:mads.sfg@gmail.com'>mads.sfg@gmail.com</a>
                        </span>
                </div> : <></>
            }
            {
                thirdLineDone ? <div style={{fontSize: '100%'}}>{createLinuxPath()}
                    <BlinkingText selectedTheme={selectedTheme} setPrevLineDone={setFourthLineDone} speed={2}
                                  words={['grep "phonenumber" mads_norby/employee_data.json']}/>
                </div> : <></>
            }
            {
                fourthLineDone ? <div style={{fontSize: '100%', color: terminalTextColor}}>{' > '}
                    <span
                        style={{color: 'red', fontWeight: 'bold'}}>phonenumber</span>: <a href='tel:+393757465060'>+39 3757 465 060</a>
                </div> : <></>
            }
            {
                fifthLineDone ? <div style={{fontSize: '100%'}}>{createLinuxPath()}
                    <BlinkingText selectedTheme={selectedTheme} setPrevLineDone={setSixthLineDone} speed={2}
                                  words={['echo $LINKED_IN_PROFILE']}/>
                </div> : <></>
            }
            {
                sixthLineDone ? <div style={{fontSize: '100%', color: terminalTextColor}}>{' > '}
                    <a target="_blank" href='https://www.linkedin.com/in/mads-norby/'>linkedin.com/in/mads-norby/</a>
                </div> : <></>
            }
        </div>
    </div>)


    return terminalWindow();

}

export type TerminalWindowProps = {
    selectedTheme:Theme
    firstLineDone: boolean
};

export default TerminalWindow;

