import React, {CSSProperties} from "react";
import {Activity} from "../ExperienceInfoCard/experience";
import {Theme} from "../../../typings";
import './style.css'

const styles = {
    activityContainer: {
        boxShadow: '0 3px 10px rgb(0 0 0 / 0.2)',
        padding: '2%',
        margin: '1%',
    },
    activityHeader: {
        fontSize: '90%',
        padding: '1%',
        fontWeight: 'bold',
        top: '-15px',
        left: '-20px'
    },
    activityText: {
        fontWeight: 'bold',
        fontSize: '70%'
    }
}

const ActivityCard: React.FC<ActivityCardProps> = ({activity, selectedTheme, textStyle}) => {
    return (
        <div style={
            {
                ...styles.activityContainer,
                position: 'relative',
                backgroundColor: selectedTheme.secondaryColor,
            }}>
            <h5 className='activity-header' style={
                {
                    ...styles.activityHeader,
                    backgroundColor: selectedTheme.mainColor,
                    border: '2px solid ' + selectedTheme.tertiaryColor,
                    color: selectedTheme.tertiaryColor,
                    position: 'absolute'
                }
            }>
                {activity.title}
            </h5>
            <span style={{...styles.activityText, ...textStyle}}>{activity.description}</span>
        </div>
    )
}

type ActivityCardProps = {
    activity: Activity
    selectedTheme: Theme
    textStyle?: CSSProperties
};

export default ActivityCard;
