import React from "react";
import InfoButton from "../MenuButton";
import {Theme} from "../../../typings";

const Navigation: React.FC<NavigationProps> = ({selected, setSelected, selectedTheme}) => {

    const removeSelectedState = () => {
        const selected =  document.querySelector('.nav-btn-selected');
        if(selected) selected.classList.remove('nav-btn-selected');
    };

    const renderNavButtons = (selected: string) => {
        const buttons = ['experience', 'education', 'contact']
        return buttons.map(
            button =>
                <InfoButton selectedTheme={selectedTheme} key={button} text={button} selected={selected===button
                } onClick={() => {
            removeSelectedState()
            setSelected(button);
        }}/>)
    };

    return (
        <div>
            <div style={{flexDirection: 'column'}}>
                {renderNavButtons(selected)}
            </div>
        </div>
    );

}

export type NavigationProps = {
    selected: string
    setSelected: (selected: string) => void
    selectedTheme: Theme
};

export default Navigation;

