import React from "react";
import './style.css'
import {NavigationProps} from "./index";
import MobileNavButton from "./MobileNavButton";
import { WorkHistory, School, ConnectWithoutContact } from '@mui/icons-material';

const MobileNavigation: React.FC<NavigationProps> = ({selected, setSelected, selectedTheme}) => {
    const removeSelectedState = () => {
        const selected = document.querySelector('.nav-btn-selected');
        if (selected) selected.classList.remove('nav-btn-selected');
    };

    const renderNavButtons = (selected: string) => {
        const buttons = [{name:'experience', icon: <WorkHistory/>}, {name:'education', icon: <School/>}, {name:'contact', icon: <ConnectWithoutContact/>}]
        return buttons.map(
            button => <MobileNavButton
                selectedTheme={selectedTheme}
                key={button.name}
                icon={button.icon}
                selected={selected === button.name
                } onClick={() => {
                removeSelectedState()
                setSelected(button.name);
            }}/>)
    };

    return (
            <div style={{display:'flex'}}>
                {renderNavButtons(selected)}
            </div>
    );
}

export default MobileNavigation;