import React, {CSSProperties, useEffect, useState} from "react";
import {Theme} from "../../../typings";


const BlinkingText: React.FC<BlinkingTextProps> = ({words, textStyle,speed=1, setPrevLineDone, selectedTheme}) => {
    const [index, setIndex] = useState(0);
    const [subIndex, setSubIndex] = useState(0);
    const [blink, setBlink] = useState(true);
    const [reverse, setReverse] = useState(false);
    const [finished, setFinished] = useState(false);

    useEffect(() => {
        if (index === words.length - 1 && subIndex === words[index].length) {
            setFinished(true)
            if(setPrevLineDone) {
                setTimeout(() => setPrevLineDone(true), 800)
            }
            return;
        }

        if (
            subIndex === words[index].length + 1 &&
            index !== words.length - 1 &&
            !reverse
        ) {
            setReverse(true);
            return;
        }

        if (subIndex === 0 && reverse) {
            setReverse(false);
            setIndex((prev) => prev + 1);
            return;
        }

        const timeout = setTimeout(() => {
            setSubIndex((prev) => prev + (reverse ? -1 : 1));
        }, Math.max(reverse ? 75 : subIndex === words[index].length ? 1000 :
            100/speed, Math.random() * 200/speed));

        return () => clearTimeout(timeout);
    }, [subIndex, index, reverse]);


    React.useEffect(() => {
        const timeout2 = setTimeout(() => {
            setBlink((prev) => !prev);
        }, 500);
        return () => clearTimeout(timeout2);
    }, [blink]);


    return (
        <div><span id='name' style={textStyle}>{`${words[index].substring(0, subIndex)}`}</span>{blink && !finished ? <span style={{backgroundColor:selectedTheme.secondaryColor, color: selectedTheme.secondaryColor}}>|</span> : " "}</div>
    );

}

export type BlinkingTextProps = {
    words: Array<string>
    textStyle?: CSSProperties,
    speed?:number
    setPrevLineDone?:(done:boolean) => void
    selectedTheme:Theme
};

export default BlinkingText;

