import React, {ReactNode} from "react";
import Navigation from "../../components/Navigation";
import './style.css'
import ExperienceInfoCard from "../../components/ExperienceInfoCard";
import $ from 'jquery';
import BlinkingText from "../../components/BlinkingText";
import EducationInfoCard from "../../components/EducationInfoCard";
import {themes} from "../../theme";
import {Theme} from "../../../typings";
import ContactInfoCard from "../../components/ContactsInfoCard";
import MobileNavigation from "../../components/Navigation/MobileNavigation";

const styles = {
    nameStyle: {
        fontSize: '500%',
        fontWeight: '900',
    },
    navContainer: {
        width: '15%',
        margin: '2%'
    },
    mobileNavContainer:{
        display: 'none',
        width: '100vw'
    },
    contentContainer: {
        width: '75%',
        height: '65vh'
    },
    header: {
        paddingLeft: '2%',
        width: '80vw',
        height: '20vh',
    },
};

const FrontPage: React.FC<FrontPageProps> = () => {
    const [selected, setSelected] = React.useState<string>('experience');
    const [selectedTheme, setSelectedTheme] = React.useState<Theme>(themes.mainTheme)

    const renderInfoCard = (): ReactNode => {
        switch (selected) {
            case 'experience':
                return <ExperienceInfoCard selectedTheme={selectedTheme}/>
            case 'education':
                return <EducationInfoCard selectedTheme={selectedTheme}/>
            case 'contact':
                return <ContactInfoCard selectedTheme={selectedTheme}/>
            default:
                return <></>
        }
    };

    const showText = (target: string, message: string, index: number, interval: number) => {
        if (index < message.length) {
            $(target).append();
            setTimeout(() => showText(target, message, index, interval), interval);
        }
    }

    const switchTheme = () => {
        selectedTheme.name === 'main' ? setSelectedTheme(themes.alienTheme) : setSelectedTheme(themes.mainTheme);
    }

    const handleScroll = () => {

        const bottom = Math.ceil(window.innerHeight + window.scrollY) >= document.documentElement.scrollHeight

        if (bottom) {
            const mobileNav = document.getElementById('mobile-nav-container')
            if(mobileNav) mobileNav.style.bottom = '-200px';
        } else {
            const mobileNav = document.getElementById('mobile-nav-container')
            if(mobileNav) mobileNav.style.bottom = '0px';
        }
    };
    React.useEffect(() => {
        window.addEventListener('scroll', handleScroll, {
            passive: true
        });
        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, []);


    return (
        <div id='main-container'>
            <span id='theme-btn' onClick={switchTheme}>toggle.theme</span>
            <div id='card' style={{backgroundColor: selectedTheme.mainColor}}>
                <div id={'header'} style={{
                    ...styles.header, display: 'flex', height: '14vh',
                    flexDirection: 'column'
                }}>
                    <span style={{...styles.nameStyle, color: selectedTheme.secondaryColor}}>mads.norby</span>
                    <div style={{fontSize: '1.5rem'}}>{' > '}
                        <BlinkingText selectedTheme={selectedTheme} speed={4}
                                      words={['mobile.developer', 'web.developer', 'frontend.developer']}/>
                    </div>

                </div>
                <div style={{display: 'flex', width: '100%'}}>
                    <div id={'nav-container'} style={styles.navContainer}>
                        <Navigation selectedTheme={selectedTheme} setSelected={setSelected} selected={selected}/>
                    </div>
                    <div id={'content-container'} style={styles.contentContainer}>
                        {renderInfoCard()}
                        <div style={{position:'relative', width: '100vw'}}>
                            <div id={'mobile-nav-container'} style={{...styles.mobileNavContainer, position:'fixed', bottom:'0px'}}>
                                <MobileNavigation selectedTheme={selectedTheme} setSelected={setSelected} selected={selected}/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div id='left-side' style={{backgroundColor: selectedTheme.secondaryColor}}></div>
            <div id='right-side-top' style={{backgroundColor: selectedTheme.tertiaryColor}}></div>
            <div id='right-side-bottom' style={{backgroundColor: selectedTheme.secondaryColor}}></div>
        </div>
    );

}

export type FrontPageProps = {};

export default FrontPage;

