import React, {CSSProperties, ReactNode} from "react";
import {Theme} from "../../../typings";

const styles = {
    card: {
        width: '100%',
        height: '100%',
        boxShadow: '0 3px 10px rgb(0 0 0 / 0.2)'
    }
}

const InfoCard: React.FC<InfoCardProps> = ({children, selectedTheme, style}) => {

    return (
        <div style={{...styles.card, ...style, position:'relative', backgroundColor:selectedTheme.mainColor}}>
            {children}
        </div>
    );
}

export type InfoCardProps = {
    children: Array<ReactNode>|ReactNode,
    selectedTheme: Theme
    style?: CSSProperties
};

export default InfoCard;

