import React, {ReactNode} from "react";
import TechnologiesGrid from "../TechnologiesGrid";
import {Theme} from "../../../typings";
import ActivityCard from "../Activity";


const Experience: React.FC<ExperienceProps> = ({ title, text, activities, technologies, selectedTheme, startDate, endDate}) => {

    const renderActivities = (activities: Array<Activity>) => {
        return activities.map(activity => (
            <ActivityCard key={activity.title} activity={activity} selectedTheme={selectedTheme}/>
        ))
    };

    return (
        <div style={{padding: '2%'}}>
            <div style={{position: 'relative'}}>
                <div className='technology-container' style={{width: '30vw', position: 'absolute', top: '0px', right: '0px'}}>
                    <TechnologiesGrid logos={technologies}/>
                </div>
            </div>
            <div style={{display: 'flex', height: '100%', flexDirection: 'row'}}>
                <div style={{width: '100%', paddingTop: '1%'}}>
                    <div className='experience-header'>
                        <h3 style={{fontSize: '90%', fontWeight: 'bold'}}>{title}</h3>
                        <div style={{maxWidth:'70%'}}>
                            <span style={{fontSize: '90%'}}>{text}</span>
                        </div>
                    </div>
                    <div className={'activities-container'} style={{display: 'flex', marginTop: '4%'}}>
                        {renderActivities(activities)}
                    </div>
                </div>
            </div>
            <div style={{height: '100%'}}>
            </div>
        </div>
    )
}

export type Activity = {
    title: string
    description: string,
    icon?: ReactNode
}

export type ExperienceProps = {
    title: string
    text: string
    activities: Array<Activity>
    technologies: Array<string>
    selectedTheme: Theme
    startDate:string
    endDate:string
};
export default Experience;