import React from "react";
import {experiences} from './experiences'
import {Theme} from "../../../typings";
import Experience from "./experience";
import './style.css'
import {Accordion, Card} from "react-bootstrap";
import {Activity} from "./experience";
import CustomAccordionHeader from "./CustomAccordionHeader";

const ExperienceInfoCard: React.FC<ExperienceInfoCardProps> = ({selectedTheme}) => {
    const renderAccordionItem = (experience: ExperienceData, i:number) => (
        <Accordion.Item key={experience.title}  eventKey={'' + i}>
            <Card style={{border:'none'}}>
                <Card.Header style={{padding:'0px',border:'none'}}>
                    <CustomAccordionHeader eventKey={'' + i}>
                            <span>{experience.title} at {experience.name}</span>
                            <div className='accordion-date' style={{
                                backgroundColor:experience.dateEnd==='now'?'rgba(189, 220, 189, .8)':'rgba(255,139,142, .8)',
                                borderRadius:'50px',
                                paddingLeft:'.5%',
                                paddingRight:'.5%'
                            }}>
                                {experience.dateStart + ' - ' + experience.dateEnd}
                            </div>
                    </CustomAccordionHeader>
                </Card.Header>
                <Accordion.Collapse eventKey={'' + i}>
                    <Card.Body>
                        <Experience
                            startDate={experience.dateStart}
                            endDate={experience.dateEnd}
                            title={experience.title}
                            text={experience.experienceText}
                            technologies={experience.technologies}
                            activities={experience.activities}
                            selectedTheme={selectedTheme}
                    /></Card.Body>
                </Accordion.Collapse>
            </Card>
        </Accordion.Item>
    )

    const renderContent = () => (<Accordion defaultActiveKey="0">
            {experiences.map((experience, i) => renderAccordionItem(experience, i))}
        </Accordion>)

    return (renderContent())

}

export type ExperienceData = {
    name: string
    logo: string
    color: string
    textColor: string
    title: string
    experienceText: string
    technologies: Array<string>
    dateStart: string
    dateEnd: string
    activities: Array<Activity>
}

export type ExperienceInfoCardProps = {
    selectedTheme: Theme
};
export default ExperienceInfoCard;