import React from "react";
import InfoCard from "../InfoCard";
import {Theme} from "../../../typings";
// @ts-ignore
import githubLogo from '../../res/images/github.png';
// @ts-ignore
import linkedInLogo from '../../res/images/linked.png'
import './style.css'
import TerminalWindow from "../TerminalWindow";

const ContactInfoCard: React.FC<ContactInfoCardProps> = ({selectedTheme}) => {

    const renderContent = () => (
        <div style={{display:'flex', justifyContent:'space-between', flexDirection:'column', height: '100%', width: '100%'}}>
            <TerminalWindow key={'terminal'} firstLineDone={true} selectedTheme={selectedTheme}/>
            <div style={{display: 'flex', justifyContent: 'space-around', alignItems: 'center', height: '100%'}}>

                <div className='contact-icon' style={{width: '5vw'}}>
                    <a target="_blank" href='https://github.com/xrosby/'>
                        <img style={{width: '100%', height: '100%'}} alt='github' src={githubLogo}/>
                    </a>
                </div>
                <div className='contact-icon' style={{width: '5vw'}}>
                    <a target="_blank" href='https://www.linkedin.com/in/mads-norby/'>
                        <img style={{width: '100%', height: '100%'}} alt='github' src={linkedInLogo}/>
                    </a>
                </div>
            </div>
        </div>
    )

    return (
        <InfoCard selectedTheme={selectedTheme}>
            {renderContent()}
        </InfoCard>);

}


export type ContactInfoCardProps = {
    selectedTheme: Theme
};
export default ContactInfoCard;
