import React from "react";
import './style.css'
import {InfoButtonProps} from "../MenuButton";

const styles = {
    btnStyle: {width:'33.3vw',
    height:'6vh',
    backgroundColor:"#232b2b",
    justifyContent:'center',
    alignItems:'center',
    fontSize: 'large',
    }
};


const MobileNavButton: React.FC<InfoButtonProps> = ({onClick, selected, selectedTheme, icon}) => {
    const className = selected?' mobile-nav-btn-selected':'';
    const colorSelected = selectedTheme.secondaryColor;
    return (
        <div className={'mobile-nav-btn ' + className} onClick={onClick} style={{...styles,color: selected?colorSelected:selectedTheme.mainColor, display:'flex',... styles.btnStyle}}>
            {icon}
        </div>
    );
}


export default MobileNavButton;