import React, {ReactNode} from "react";
import './style.css'
import {Theme} from "../../../typings";

const styles = {
    fontSize: '1.5vw',
    marginTop: '1%',
    paddingLeft:'2%',
    borderBottomRightRadius: '100px',
    borderTopRightRadius: '100px'

};


const InfoButton: React.FC<InfoButtonProps> = ({text, onClick, selected, selectedTheme}) => {
    const className = selected?' nav-btn-selected':'';
    return (
        <div className={'nav-btn ' + className} onClick={onClick} style={{...styles,color: selectedTheme.tertiaryColor,
            backgroundColor:selectedTheme.secondaryColor,}}>
           <span> {selected?'>':'-'} {text}</span>
        </div>
    );
}

export type InfoButtonProps = {
    text?: string
    onClick: () => void
    selected: boolean
    selectedTheme: Theme
    icon?: ReactNode
};
export default InfoButton;