import React from "react";

const TechnologiesGrid: React.FC<TechnologiesGridProps> = ({logos}) => {

    const renderLogos = () => {
        return logos.map((logo, i) => (
            <div key={'techlogo-' + i} style={{width: '3vw'}}><img style={{width:'100%'}} alt={'logo-' + i} src={logo}/></div>
        ))
    };

    return (
            <div style={{
                display:'grid',
                justifyContent:'flex-end',
                gridTemplateColumns: 'auto auto auto auto'
            }}>
                {renderLogos()}
        </div>
    );

}

export type TechnologiesGridProps = {
    logos:Array<string>
};

export default TechnologiesGrid;

